import React from "react";
import "./Consoles.scss";
import axios from "axios";
import { useEffect, useState } from "react";
import { MenuOpenSharp } from "@material-ui/icons";
export default function Consoles({ data, setData }) {
  const [console_data, setConsole_data] = useState([]);
  useEffect(() => {
    axios("https://roms-scraper.herokuapp.com/api/consoles_list").then(
      (res) => {
        setConsole_data(res.data);
      }
    );
  }, []);

  return (
    <div id="consoles-parent">
      <div
        id="open-menu"
        onClick={() => {
          document.getElementById("consoles_list").style.transform =
            "translateX(0px)";
        }}
      >
        <MenuOpenSharp />
      </div>
      <div id="consoles_list">
        <MenuOpenSharp
          style={{
            display: "flex",
            marginRight: "auto",
            margin:'10px'
          }}
          onClick={() => {
            document.getElementById("consoles_list").style.transform =
              "translateX(-300px)";
          }}
        />
        {console_data.map((items) => (
          <>
            <div
              onClick={() => {
                for(let i=1;i<=8;i++){
                    setData([]);
                    axios(`https://roms-scraper.herokuapp.com/api/console/${i}?name=${items.code_name}`)
                    .then(res=>{
                        console.log(res.data)
                        res.data.forEach(items=>{
                          setData(prev=>[...prev,items])
                        })
                    })
                }
              }}
            >
            <img src={items.image} alt="" id='consoles_image' />
              {items.name}

            </div>
            <br />
          </>
        ))}
      </div>
    </div>
  );
}
